import React from 'react';

export default function DocenteDashboard() {
  return (
    <div className="flex min-h-screen bg-gray-900">
      {/* Sidebar */}
      <aside className="w-64 bg-gray-800 text-white flex flex-col items-center py-8">
        <div className="flex flex-col items-center mb-8">
          <img
            src="/profile-placeholder.png"
            alt="Foto de perfil"
            className="w-20 h-20 rounded-full mb-2 border-4 border-green-500"
          />
          <span className="font-bold text-lg">Profesora Ana</span>
          <span className="text-xs text-gray-400">ID: 12345</span>
        </div>
        <nav className="w-full">
          <ul>
            <li>
              <a
                href="#"
                className="flex items-center px-6 py-3 bg-green-600 rounded-lg font-semibold"
              >
                <span className="ml-2">Notas</span>
              </a>
            </li>
            {/* Aquí se pueden agregar más opciones en el futuro */}
          </ul>
        </nav>
      </aside>
      {/* Main Content */}
      <main className="flex-1 p-10 text-white">
        <h1 className="text-3xl font-bold mb-8">Panel de Control del Docente</h1>
        {/* Aquí va el contenido de la función de notas */}
        <div className="bg-gray-800 rounded-lg p-8 shadow-lg">
          <h2 className="text-xl font-semibold mb-4">Gestión de Notas</h2>
          <p className="text-gray-300">Aquí irá la funcionalidad de notas para testing rápido.</p>
        </div>
      </main>
    </div>
  );
}
